<template>
  <div class="sombra formulario">
    <h1>Bibloteca UACH</h1>
    <form @submit.prevent="estatusEditar ? updateTodo() : addTodo()">
      <legend>Ingresa los datos del libro a registrar</legend>
        <p>
          <span>Titulo -></span>
          <input type="text" v-model="titulo">
          <button class="boton2" v-if="estatusEditar" @click="estatusEditar= false, titulo='' ">Cancelar</button>
        </p>
        <p>
          <span>Autor -></span>
          <input type="text" v-model="autor">
          <button class="boton2" v-if="estatusEditar" @click="estatusEditar= false, autor='' ">Cancelar</button>
        </p>
        <p>
          <span>ID -----></span>
          <input type="number" v-model="serial">
          <button class="boton2" v-if="estatusEditar" @click="estatusEditar= false, serial='' ">Cancelar</button>
        </p>
        <p>
          <button class="boton" type="submit">{{ estatusEditar ? "Editar": "Agregar" }}</button>
        </p>
      </form>
      <img src="https://pa1.narvii.com/6707/510b0daee67fbc091f14b9d8ef40aeb6c0d4dc7d_hq.gif" v-if="cargando">

      {{estatusEditar}}

      <ul>
        <li v-for="todo in todos" :key="todo.id">
        {{todo.titulo}} | {{todo.autor}} | {{todo.serial}}
        <button class="boton2" @click="deleteTodo(todo)">Eliminar</button>
        <button class="boton2" @click="selectTodo(todo)">Editar</button>
        </li>
      </ul>
  </div>
</template>

<script>

import {db} from './firebase'

export default {
  name: 'App',
  data() {
    return {
      todos: [],
      serial: "",
      titulo: "",
      autor:"",
      cargando: false,
      estatusEditar: false
    }
  },
  created() {
    this.listarTodos();
  },
  methods: {
    async listarTodos(){

          this.cargando = true;
          const data = await db.collection("todos").get();
          this.todos = data.docs.map(doc => ({
            id: doc.id, ...doc.data()
          }))
          this.cargando = false;
          console.log(this.todos);

    },
    async addTodo() {
        await db.collection('todos').add(
          {
            titulo:  this.titulo,
            autor:  this.autor,
            serial:  this.serial
          }
        )
        this.titulo = "";
        this.autor = "";
        this.serial = "";
        this.listarTodos();
    },
    async deleteTodo(todo){

      if(confirm(`Estas seguro que desaeas eliminar ${todo.titulo} | ${todo.autor} | ${todo.serial}`)){
        await db.collection('todos').doc(todo.id).delete();
        this.listarTodos();
      }
    },
    selectTodo(todo){
      this.estatusEditar = true;
      this.titulo = todo.titulo;
      this.autor = todo.autor;
      this.serial = todo.serial;
      this.id = todo.id;
    },
    async updateTodo(){
      await db.collection('todos').doc(this.id).set(
        {
          titulo:  this.titulo,
          autor:  this.autor,
          serial:  this.serial
        }
      )
      this.estatusEditar = false;
      this.titulo = "";
      this.autor = "";
      this.serial = "";
      this.listarTodos();
    }
  },  
}
</script>

